<template>
    <div class="row">
        <div class="col col-12">
            <button class="login__back mt-2" @click="$router.go(-1)">
                <img src="../assets/arrow_left.svg" alt="back">
            </button>
            <h2 class="login__h2">Login</h2>
            <form class="login__form">
                <div class="login__wrap-field">
                    <label for="email" class="login__label">E-mail</label>
                    <input type="email" v-model="email" placeholder="E-mail" id="email" class="login__input"
                    required>
                </div>
                <div class="login__wrap-field">
                    <label for="password" class="login__label">Password</label>
                    <input type="password" v-model="password" placeholder="Password" id="password" class="login__input"
                    required>
                </div>
            </form>
            <div class="text-center justify-space-around">
                <div class="login__btn-login-wrap">
                    <button @click="handleLogin" class="login__btn-login">Login</button>
                    <ul v-if="errors.email">
                        <li v-for="error of errors.email" :key="error" class="text-danger">
                            {{ error }}
                        </li>
                    </ul>
                    <ul v-if="errors.password">
                        <li v-for="error of errors.password" :key="error" class="text-danger">
                            {{ error }}
                        </li>
                    </ul>
                </div>
                <div class="login__or">or</div>
<!--                <button @click="handleLoginGoogle" class="login__btn-login-google">
                    <span class="wrap_img">
                        <img src="../assets/google_logo.svg" alt="google_logo">
                    </span>
                    <span>Login with Google</span>
                </button>-->
                <div class="login__links">
<!--                    <router-link to="/forgot-password" class="login__link">Forgot password?</router-link>-->
                    <router-link to="/sign-up" class="login__link">Register</router-link>
                </div>
            </div>
        </div>
    </div>
</template>

<script>

export default {
    name: "Login",
    components: {},
    data: () => ({
        email: '',
        password: '',
        errors: {},
    }),
    mounted() {
        console.log('Login page');
    },
    methods: {
        async handleLogin() {
            if (this.email && this.password) {
                const {email, password} = this;
                this.$auth.login({data: {email, password}, redirect: null})
                    .then(res => {
                        let route = res.data.data.roles.findIndex(role => role === 'Admin') > -1
                            ? this.$auth.options.loginData.redirect.admin
                            : '/home'

                        this.$router.push(route)
                    })
                    .catch((error) => {
                        this.errors = error.response.data.errors;
                        setTimeout(() => this.errors = {}, 7000)
                    })
            }

        },
        handleLoginGoogle() {

        }
    }
}
</script>

